#TopbarView{height:50px;background:white;border-bottom:1px solid #eceeef;box-sizing:content-box;display:flex;justify-content:flex-end;font-family:"GTHaptik-Medium"}#TopbarView nav{display:flex;margin-left:20px}#TopbarView nav#options{background:#f7f7f9}#TopbarView nav#options div{cursor:pointer;padding:10px}#TopbarView nav#options div:hover{background:#eceeef}#TopbarView nav#options div:hover.logout i{background-position:-48px -24px}#TopbarView nav#options div:hover.settings i{background-position:-72px -24px}#TopbarView nav#profile .name{padding:10px}#TopbarView nav#profile .photo{padding:2.5px}#TopbarView nav#profile .photo img{width:40px;height:40px;border-radius:100%;overflow:hidden}#TopbarView .notification,#TopbarView .account{display:flex;cursor:pointer}#TopbarView .notification>div,#TopbarView .account>div{margin-right:10px}#TopbarView .notification{margin-right:50px;padding-top:13px}#TopbarView .notification .number{background:#F2C300;color:white;font-size:.75rem;padding:2px 5px;text-align:center;margin-top:-10px;margin-left:-19px;height:21px;border:3px solid #fff}#TopbarView .notification .number.single,#TopbarView .notification .number.double{border-radius:100%;width:16px}#TopbarView .notification .number.tripple{border-radius:20px}#TopbarView .account{display:flex;flex-direction:column;min-height:100px;height:auto;position:relative}#TopbarView .account #visible{display:flex}#TopbarView .account #visible *{margin-right:5px}#TopbarView .account #visible .revealOptions{width:10px;padding-top:27px;color:#00A550;zoom:0.7}#TopbarView .account #visible .revealOptions:hover{color:#007237}#TopbarView .account #hiddenOptions{width:100%;background:#ffffff;box-shadow:1px 2px 3px #ccc;visibility:hidden}#TopbarView .account #hiddenOptions .options{padding:10px;font-size:.875rem;color:#31393A}#TopbarView .account #hiddenOptions .options i{margin-right:5px}#TopbarView .account #hiddenOptions .options:hover{background-color:#f9f9f9}
